section {
	background-color: #1c1e21;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	color: #e4e6eb;
	line-height: 1.5 !important;
}

@media (max-width: 1028px) {
	.container,  .container-fluid {
		padding: 5% 0%;
		padding-bottom: 15% !important;
	}

	.about-title h2, h5{
		/* text-align: center; */
	}

	#about-image {
		max-width: 100%;
		height: auto;
	}

	.card-img-top {
		max-width: 100%;
		height: auto;
	}

	.section-title {
		text-align: center;
		margin-bottom: 10% !important;
	}
}

.program-icon {
	color: #fff;
}

.container, .container-fluid {
	padding: 0 15%;
	padding-bottom: 5%;
}

.section-title {
	margin-bottom: 3%;
	border-bottom: 2px solid;
}

/* nav */
.nav-link {
	padding-right: 20px !important;
	padding-left: 20px !important;
}

/* About */

.about-button {
	margin: 3% 3% 5% 0;
	background-color: #828282;
}

/* Skill */
.card .card-body a {
	color: #e4e6eb;
}

/* Contact */
.form-label {
	color: #e4e6eb;
}
